//import logo from './logo.svg';

import React, {useState, useEffect} from 'react';
import './App.css';
import Nav from './Nav';
import { BrowserRouter, Route, Switch, Link, Redirect } from 'react-router-dom';

import IMG_TEAMS from './Team-1900x1267.jpg';
import IMG_CELLS from './cells.jpg';
import IMG_CELLY from './celly.jpg';
import IMG_CONTACTS from './Contact.jpg';

function App() {
  const [link, setLink] = useState("./HOME");

  const NAV_ITEMS = ["HOME", "COMPANY", "About us", "Team", "PRODUCTS", "Celector", "Application", "RESEARCH", "CONTACTS"];
  const TEXT_FOOT_1 = "Stem Sel s.r.l.\nViale Giuseppe Fanin, 48\n40127 Bologna, Italy\nTel: +39 0516336638";
  const TEXT_FOOT_2 = "P. IVA 03331751200\nCapitale sociale 22.712,74 €\nREA: BO-510992";


  /////////////////////////////////////////////////////////////////////////////////////////////////// 
  function NewlineText(props) {
    const tex = props.text;
    const newText = tex.split('\n').map(str => <p>{str}</p>);
    
    return newText;
  }

  ///////////////////////////////////////////////////////////////////////////////////////////////////
  useEffect(() => {

    function ShowBackImage() {
      let x = document.getElementById("AppUp_div");
      if (("./RESEARCH") === link) 
      {
        x.style.backgroundImage = 'url(' + IMG_CELLS + ')';
      } 
      else if (("./CONTACTS") === link)  
      {
        x.style.backgroundImage = 'url(' + IMG_CONTACTS + ')';
      }
      else /*if (("./COMPANY") === link)  */
      {
        x.style.backgroundImage = 'url(' + IMG_CELLY + ')';
      }
    } 

    ShowBackImage();

  }, [link]);

  

  return (
    <div className="App">

      <div id="AppUp_div" className="App-Up">
        <Nav setLink={setLink}  items={NAV_ITEMS} />
      </div>


      <BrowserRouter>


            <Redirect to={link} />

            <hr />

            <Switch>
              <Route exact path={"/"+NAV_ITEMS[0]}> 
                <div id="home_div"> 
                  <p> HOME </p>
                  <hr />
                  <br></br>
                  <br></br>
                  <hr />
                </div>               


                <div> 
                  <p> PARTNERS </p>
                  <hr />
                  <br></br>
                  <br></br>
                  <hr />
                </div>

                
              </Route>

              <Route exact path="/home">  
                <Redirect to="/HOME" />
              </Route>

              <Route exact path="/">  
                <Redirect to="/HOME" />
              </Route>

              <Route exact path={"/"+NAV_ITEMS[1]}>  
                <div id="company_div"> 
                  <p> COMPANY </p>
                  <hr />
                  <br></br>
                  <br></br>
                  <hr />
                </div>
              </Route>  

              <Route exact path={"/"+NAV_ITEMS[2]}>  
                <div id="about_div"> 
                  <p> ABOUT US </p>
                  <hr />
                  <br></br>
                  <br></br>
                  <hr />
                </div>
              </Route>  

              <Route exact path={"/"+NAV_ITEMS[3]}>  
                <div id="team_div"> 
                  <p> TEAM </p>
                  <hr />
                  <br></br>
                  <br></br>
                  <hr />
                </div>
              </Route>

              <Route exact path={"/"+NAV_ITEMS[4]}>  
                <div id="product_div"> 
                  <p> PRODUCT </p>
                  <hr />
                  <br></br>
                  <br></br>
                  <hr />
                </div>
              </Route>
              
              <Route exact path={"/"+NAV_ITEMS[5]}>  
                <div id="celector_div"> 
                  <p> CELECTOR </p>
                  <hr />
                  <br></br>
                  <br></br>
                  <hr />
                </div>
              </Route>

              <Route exact path={"/"+NAV_ITEMS[6]}>  
                <div id="appplication_div"> 
                  <p> APPLICATION </p>
                  <hr />
                  <br></br>
                  <br></br>
                  <hr />
                </div>
              </Route>

              <Route exact path={"/"+NAV_ITEMS[7]}>  
                <div id="research_div"> 
                  <p> RESEARCH </p>
                  <hr />
                  <br></br>
                  <br></br>
                  <hr />
                </div>
              </Route>

              <Route exact path={"/"+NAV_ITEMS[8]}>  
                <div id="contact_div"> 
                  <p>CONTACTS</p>
                  <hr />
                  <br></br>
                  <br></br>
                  <hr />
                 </div>
              </Route>

              <Route exact path="/login">

              </Route>



            </Switch>
          

          <div className="App-Foot">
            <div id="foot-it_1" className="App-Foot-Item">
              <NewlineText text={TEXT_FOOT_1} />
            </div>

            <div id="foot-it_2" className="App-Foot-Item">
              <NewlineText text={TEXT_FOOT_2} />
            </div>

            <div id="foot-it_3" className="App-Foot-Item">
              <Link to="/HOME">HOME</Link>
            </div>
          </div>
      </BrowserRouter>
        


      <p id="copyright"> Copyright © - Stem Sel s.r.l. - P.IVA 03331751200 </p>

    </div>
  );
}

export default App;

