import React, {useState/*, useEffect*/} from 'react';

import logo from './CELECTOR_LOGO_NEW_32X32.png';
import './Nav.css';

    // https://fireship.io/lessons/dropdown-menu-multi-level-react/




function Nav({setLink, /*logo,*/ items/*, item1, item2, item3, item4*/}) {
    const [dropOpen, setDropOpen] = useState("0");

    const HandleClick = value => () => {
        setLink("./" + value);
    }

/*
    function NavItem(props) {
        const [open, setOpen] = useState(false);
      
        return (
          <li className="nav-item">
            <a href="#" className="icon-button" onClick={() => setOpen(!open)}>
              {props.icon}
            </a>
      
            {open && props.children}
          </li>
        );
      }*/



    function DropdownMenu(props) { 
        let item = null;


        function DropdownItem(props) {
            return (
            <a className="Nav-DropDownItem" onClick={HandleClick(props.text)}>
                {props.text}
            </a>
            );
        }

        if(props.id === ("d"+dropOpen))
        {
            if(dropOpen === "0") {
                item = null;
            }
            else if(dropOpen === "1") {
                item = <div className="Nav-Dropdown" onMouseLeave={() => setDropOpen("0")}>
                    <DropdownItem text={props.textSub1}/>
                    <DropdownItem text={props.textSub2}/>
                </div>;
            }
            else if(dropOpen === "2") {
                item = <div className="Nav-Dropdown" onMouseLeave={() => setDropOpen("0")}>
                    <DropdownItem text={props.textSub1}/>
                    <DropdownItem text={props.textSub2}/>
                </div>;
            }
        }


    return (
            item
        )
    }
//                 item = <div className="Nav-Dropdown" style={{position: "relative", left: leftOff, color: "orange"}}>

    function NavItem(props) {
   //     const [open, setOpen] = useState(false);
  
        return (
          <li className="Nav-Item"  onMouseEnter={() => setDropOpen(props.id)} /*onMouseLeave={() => setDropOpen("0")}*/ >
            <a className="Nav-Item-Link" onClick={HandleClick(props.text)}> {props.text} </a>
            {props.children}
          </li>  
        );
            //    <DropdownMenu id={"d"+props.id} textSub1={props.textSub1} textSub2={props.textSub2} />
    }

 //   console.log(document.getElementById("1").getBoundingClientRect().width);  
    

  return (
    <nav className="Nav">
        <img src={logo} className="Nav-Logo" alt="---" onClick={HandleClick(items[0])} />
        <p className="Nav-Title" onClick={HandleClick(items[0])}> Stem Sel </p>

        <ul className="Nav-List" onMouseLeave={() => setDropOpen("0")}>
            <NavItem id="1" text={items[1]} textSub1={items[2]} textSub2={items[3]}>
                <DropdownMenu id="d1" textSub1={items[2]} textSub2={items[3]}/>
            </NavItem>
            <NavItem id="2" text={items[4]} textSub1={items[5]} textSub2={items[6]}>
                <DropdownMenu id="d2" textSub1={items[5]} textSub2={items[6]}>

                </DropdownMenu>

            </NavItem>
            <NavItem id="3" text={items[7]} />
            <NavItem id="4" text={items[8]} />
        </ul>
    </nav>
  );
}

export default Nav;


